<template>
  <v-container grid-list-sm fluid ref="videoContainer" class="scroll-y" style="max-width: 90rem">
    <v-layout  row wrap id="videoLayout" ref="videoLayout">

      <!--@touchstart="touchPosition($event)"
        @touchmove="pullMove($event)"-->
      <v-flex
        class="videoList"
        id="videoList"
        ref="videoList"
        v-for="(item,index) in this.$store.state.videoList"
        :key="index"
        xs12
        sm6
        md4
      >
        <!--hover焦点阴影效果-->
        <v-hover>
          <v-card tile class="d-flex pa-0" style="cursor: pointer; border-radius: 0.5rem"
                  id="videoItem"
                  ref="videoItem"
                  v-ripple
                  slot-scope="{ hover }"
                  :class="`elevation-${hover ? 12 : 2}`">
            <!--<v-img-->
            <!--aspect-ratio="1.5"-->
            <!--:src="`https://picsum.photos/500/300?image=${n * 5 + 10}`"-->
            <!--:lazy-src="`https://picsum.photos/10/6?image=${n * 5 + 10}`"-->
            <!--aspect-ratio="1"-->
            <!--class="grey lighten-2"-->
            <!--/>-->
            <v-img
              height="15rem"
              aspect-ratio="1.5"
              :src="`${item.imgPath}`"
              :lazy-src="`${item.imgPath}`"
              @click="imgPlayer(index,item)"
              class="grey lighten-2"
            >
              <v-container fill-height fluid class="pa-0">
                <v-layout align-end white--text wrap class=" ma-1">
                  <v-layout class="imgText">
                    <v-flex xs10 text-xs-left lexbox>
                      <v-layout row wrap>
                        <span>{{item.title}}</span>
                      </v-layout>
                      <v-layout row wrap>
                        <span>{{item.tags}}</span>
                      </v-layout>
                    </v-flex>
                    <v-flex xs2>
                      <v-layout justify-end>
                        <v-icon small color="white">thumb_up</v-icon>
                      </v-layout>
                      <v-layout justify-end row wrap class="mr-1 mt-1 mb-1">
                        <span>{{item.praise}}</span>
                      </v-layout>
                    </v-flex>
                  </v-layout>
                </v-layout>
              </v-container>
            </v-img>
          </v-card>
        </v-hover>
      </v-flex>
    </v-layout>

    <!--在屏幕中间显示 共用等待框-->
    <v-dialog v-model="this.$store.state.waitDialog" hide-overlay persistent width="20rem">
      <v-card color="primary" dark>
        <v-card-text>
          Please stand by
          <v-progress-circular
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-circular>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!--下拉加载-->
    <slot></slot>
    <slot name="load-more" class="mt-2 mb-5">
      <v-layout justify-center class="mt-2 mb-5" v-if="this.pullUpState==1">
        <span>{{pullUpStateText.moreDataTxt}}</span>
      </v-layout>
      <v-layout justify-center class="mt-2 mb-5" v-if="this.pullUpState==2">
        <!--<span >{{pullUpStateText.loadingMoreDataTxt}}</span>-->
        <v-progress-circular
          indeterminate
          color="blue"
          class="mb-0"
        ></v-progress-circular>
      </v-layout>
      <v-layout justify-center class="mt-2 mb-5" v-if="this.pullUpState==3">
        <span >{{pullUpStateText.noMoreDataTxt}}</span>
      </v-layout>
    </slot>
  </v-container>
</template>

<script>
import contractABI from '../js/Ethereum'
import ipfsApi from 'ipfs-api'
// import BScroll from 'better-scroll'
export default {
  name: 'HelloWorld',
  data: () => ({
    testHash: 'QmWz7HFRrtgiPtASYPxBvCJU3WbpUdpPwRXecx24bAZHVv ', // dog jpg
    imgHash: 'QmQtBPVBPHDbviq8aNAa4nheQE8H4dPKpDmeHDPrN3pMNe', // dog jpg
    mp4Hash: 'QmZeM34MDAMCcj7k7Tr29NjoKuwF8xdhHQgV9d1uKC3SK7', // FEF801699F97483308017A9E5C2BD5A2.mp4
    selectPath: '',
    imgShow: true,
    inputStr: '',
    // 下拉加载
    initVideoQty: 9, // 列表初始加载数量
    pullUpQty: 6, // 上拉加载数量
    pullUpState: 0, // 1:提示上拉加载，2:加载中，3:没有更多数据
    pullUpStateText: { // 状态提示
      moreDataTxt: 'Pulling up load more',
      loadingMoreDataTxt: 'Loading...',
      noMoreDataTxt: 'No More '
    }
  }),
  computed: {
    // getImgByte: function (item) {
    //   return this.videoInfo[item].imgByte
    // }
  },
  beforeCreate () {
    // contractABI.initContract('fileInfo')
    this.$store.state.waitDialog = true // 先加载等待框
    contractABI.initContract(this.$store.state.sysModel)
  },
  mounted () {
    window.scrollTo(0, 0)
    // 监听滚动 用于上拉或滚动加载
    window.addEventListener('scroll', this.scrollPage, true)

    // 监听手机返回键 用于用户在按返回键是退出关闭当前页
    // 如果支持 popstate 一般移动端都支持了
    if (window.history && window.history.pushState) {
      // 往历史记录里面添加一条新的当前页面的url
      history.pushState(null, null, document.URL)
      // 给 popstate 绑定一个方法 监听页面刷新
      // 需要在destroyed中注销事件
      // window.addEventListener('popstate', this.goBack, true) // false阻止默认事件
    }
    console.log('log in ', this.$options.name)
    // contractABI.initGateway()

    // 加载视频图片列表
    contractABI.initRandomVideoList(this.initVideoQty)
  },
  methods: {

    scrollPage (e) {
      // 大于50是用于空出顶部toolbar的距离
      // console.log('scrollPage1', window.scrollY, window.innerHeight, e.target.scrollingElement.offsetHeight, e.target.scrollingElement.scrollTop)
      if (window.scrollY > 50 && window.scrollY >= (e.target.scrollingElement.offsetHeight - window.innerHeight)) {
        // 设置为正在加载中
        if (!this.$store.state.isLoading) {
          this.pullUpState = 1
          // console.log('End in window', e.target.scrollingElement.offsetHeight, e.target.scrollingElement.scrollHeight, this.$store.state.videoList.length, this.$store.state.videoCount, this.pullUpState, this.isLoading)

          if (this.$store.state.videoList.length >= this.$store.state.videoCount - this.pullUpQty) {
            this.pullUpState = 3 // 没有数据了
          } else {
            // 加载数据
            this.pullUpState = 2
            this.$store.state.isLoading = true

            this.infiniteLoad()
          }
        }
      }
    },

    infiniteLoad () {
      // contractABI.loadSplitVideoList(this.pullUpQty)
      setTimeout(() => {
        contractABI.loadSplitVideoList(this.pullUpQty)
      }, 2000)
    },
    catHash () {
      let hash = this.testHash
      if (this.inputStr !== '') {
        hash = this.inputStr
      }
      const myIpfs1 = ipfsApi('172.245.158.172', '5001', {protocol: 'http'})
      console.log('cat', myIpfs1, hash)

      myIpfs1.cat(hash)
        .then(function (response) {
          // viewControl.src = response.toString() // 显示图片hash :QmS14ULjyvsJz6BtoHoNx8X5GApL3bxfiKgGYp16oDBniC
          console.log('cat hash', hash, '\nsus', response.toString())
        })
        .catch(function (error) {
          console.log('cat hash', hash, 'error', error)
        })
    },
    getHash () {
      if (this.inputStr !== '') {
        let hash = this.inputStr
        const myIpfs = ipfsApi(this.$store.state.ipfsHost.server, this.$store.state.ipfsHost.port, {protocol: 'http'})

        myIpfs.get(hash)
          .then(function (response) {
            console.log('get hash', hash, '\nsus', response)
          })
          .catch(function (error) {
            console.log('get hash', hash, '\nerror', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    player () {
      // video.src = 'http://localhost:8090/ipfs/QmTt4EGNYppgxgqThoSNNXnJRLwCy8SwNnqdfw4DXYrzWe'
      // video.play()

      if (this.inputStr !== '') {
        const myIpfs = ipfsApi(this.$store.state.ipfsHost.server, this.$store.state.ipfsHost.port, {protocol: 'http'})
        const videoControl = this.$refs.player
        let vHash = this.inputStr // 'QmQRX5yb1taEavc48ExrYqZQXCZdqVCmHtQDBcswXmdX7A' t2.mp4
        myIpfs.cat(vHash)
          .then(function (response) {
            videoControl.src = response.toString()
            videoControl.play()
            console.log('player hash res', vHash)
          })
          .catch(function (error) {
            console.log('player hash', vHash, 'error', error)
          })
      } else {
        alert('inputStr is empty')
      }
    },
    imgPlayer (index, item) {
      // this.$router.push({path: '/Detail', query: {index: index, time: time}})
      // this.$store.commit('setVideoPlayerParam', item)
      let videoKey = index + item.time
      let videoItem = {'time': item.time.toString(), 'videoHash': item.videoHash, 'imgHash': item.imgHash, 'author': item.author, 'category': item.category, 'title': item.title, 'tags': item.tags, 'price': item.price, 'praise': item.praise.toString()}
      // console.log('go player hash: ', index, videoKey, item, JSON.stringify(videoItem))
      localStorage.setItem(videoKey, JSON.stringify(videoItem))
      // let routeData = this.$router.resolve({
      //   path: '/Detail',
      //   query: {
      //     key: videoKey
      //   }
      // })
      // window.open(routeData.href, '_blank') // 新窗口打开
      this.$router.push({path: '/Detail', query: {key: videoKey}})
    },
    goHome () {
      this.$router.push({path: '/'})
    },
    goUploadFile () {
      // this.$router.push({path: '/Detail', query: {hash: videoHash}})

      let toPage = '/uploadFile'
      // 同一页面不跳转
      if (window.location.href.indexOf(toPage) > 0) {
        return
      }

      if (window.location.hostname === 'localhost') {
        let routeData = this.$router.resolve({
          path: toPage
        })

        window.open(routeData.href, '_blank') // 新窗口打开
      } else {
        // 上传界面在https协议下会报错 强制修改为http协议中进行 （需在服务器端改善)
        window.open('http://babyhard.com/#/uploadFile', '_blank')
      }

      console.log('go: ', toPage)
    },
    goXXCoin () {
      let toPage = '/coinMarket'
      // 同一页面不跳转
      if (window.location.href.indexOf(toPage) > 0) {
        return
      }

      let routeData = this.$router.resolve({
        path: toPage
      })

      window.open(routeData.href, '_blank') // 新窗口打开
      console.log('go: ', toPage)
    },
    ssCode () {
      let url = 'https://github.com/babyhard/video/blob/master/SS%20Source%20Code.sol'
      window.open(url)
    },
    ssWhiteBook () {
      let url = 'https://github.com/babyhard/video/blob/master/ss%20whitebook.pdf'
      window.open(url)
    },
    gitHub () {
      let url = 'https://github.com/babyhard/video'
      window.open(url)
    },
    // 手机返回健执行
    goBack () {
      // this.$router.push({'name': 'HelloWorld'})
      console.log('phone go back ')

      let toPage = 'about:blank'
      // 闭关当前页
      let userAgent = navigator.userAgent
      if (userAgent.indexOf('Firefox') !== -1 || userAgent.indexOf('Chrome') !== -1) {
        window.location.href = toPage
      } else if (userAgent.indexOf('Android') > -1 || userAgent.indexOf('Linux') > -1) {
        window.opener = null
        window.open(toPage, '_self').close()
      } else {
        window.opener = null
        window.open(toPage, '_self')
        window.close()
      }
    }
  },
  destroyed () {
    // 关闭手机物理按键监听
    // window.removeEventListener('popstate', this.goBack, false) // false阻止默认事件
  }

}
</script>

<style>
  .halfTrans {
    background-color: rgba(0,0,0,0.3);
    border-radius: 1rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-bottom: 0.1rem;
  }

  .imgText {
    background: linear-gradient(to bottom, transparent, rgba(0,0,0,0.3));
  }
</style>



